<template>
    <!-- 支付方式 下拉选择 component -->
  <div class="payway-component">
    <el-select v-model="value" placeholder="请选择" @change="selectChange">
        <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
        </el-option>
    </el-select>
  </div>
</template>

<script>
import { formPayWay } from "@/config/cfg";

export default {
  data() {
    return {
      options: [
        {
          value: 0,
          label: "全部"
        },
        {
          value: formPayWay.CASHPAYMENT,
          label: formPayWay.toString(formPayWay.CASHPAYMENT)
        },
        {
          value: formPayWay.WECHATPAY,
          label: formPayWay.toString(formPayWay.WECHATPAY)
        },
        {
          value: formPayWay.ALIPAY,
          label: formPayWay.toString(formPayWay.ALIPAY)
        },
        {
          value: formPayWay.CARDPAYMENT,
          label: formPayWay.toString(formPayWay.CARDPAYMENT)
        },
        {
          value: formPayWay.IOUPAY,
          label: formPayWay.toString(formPayWay.IOUPAY)
        }
      ],
      value: ""
    };
  },
  methods: {
    selectChange() {
      this.$emit("payway-change", this.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.payway-component {
  display: inline-block;
}
</style>
<style lang="scss">
.payway-component {
  .el-select {
    width: 180px;
    height: 34px;

    .el-input {
      font-size: 14px;

      input {
        border-radius: 0;
        height: 34px;
      }
    }
  }
}
</style>




