<template>
    <div id="refundsuccess-orders">

        <!-- 搜索 start -->
        <div class="search-content">
            <div class="search-top clearfix">
                <div class="search-item left clearfix">
                    <div class="search-label left">创建时间</div>
                    <div  class="search-detail left">
                        <el-date-picker
                            @change="ordertimeStartChange"
                            v-model="orderBeginTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                    <div  class="search-detail left time">
                        <el-date-picker
                            @change="ordertimeEndChange"
                            v-model="orderEndTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                </div>
                <div class="search-item left clearfix">
                    <div class="search-label left">销售方式</div>
                    <div class="search-detail left">
                        <sale-way @dineway-change="getData('dineWay', $event)"></sale-way>
                    </div>
                </div>
                <div class="search-item left clearfix">
                    <div class="search-label left">支付方式</div>
                    <div class="search-detail left">
                        <pay-way @payway-change="getData('payWay', $event)"></pay-way>
                    </div>
                </div>
                <div class="search-item left clearfix seat">
                    <div class="search-label left">餐桌名称/餐牌号</div>
                    <div class="search-detail search-text left">
                        <el-input v-model="menuCardOrder"
                        placeholder="餐桌名称/餐牌号"></el-input>
                    </div>
                </div>
            </div>

            <div class="search-bottom clearfix">
                <div class="search-item left clearfix">
                    <div class="search-label left">退款时间</div>
                    <div  class="search-detail left">
                        <el-date-picker
                            @change="beginTimeChange"
                            v-model="beginTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                    <div  class="search-detail left time">
                        <el-date-picker
                            @change="endTimeChange"
                            v-model="endTime"
                            type="datetime"
                            placeholder="选择日期时间">
                        </el-date-picker>
                    </div>
                </div>

                <div class="search-item left clearfix">
                    <div class="search-label left">是否开票</div>
                    <div class="search-detail left">
                        <s-invoice @invoice-change="getData('isInvoicing', $event)"></s-invoice>
                    </div>
                </div>
                <div class="search-item left clearfix">
                    <div class="search-label left">订单编号</div>
                    <div class="search-detail left search-text">
                        <el-input v-model="orderId"
                        placeholder="订单编号"></el-input>
                    </div>
                </div>
                <div class="search-item left clearfix seat">
                    <el-button class="searchBtn right" @click="getList">搜索</el-button>
                </div>
            </div>
        </div>
        <!-- 搜索 end -->

        <!-- 汇总 start -->
        <div class="summery-content clearfix">
            <div class="summery-item left">
                <summery-card :summeryObj="totalNum"></summery-card>
            </div>
            <div class="summery-item left">
                <summery-card :summeryObj="totalPrice"></summery-card>
            </div>
            <div class="summery-item left">
                <summery-card :summeryObj="totalRealPrice"></summery-card>
            </div>
            <div class="summery-item left">
                <summery-card :summeryObj="totalAveragePrice"></summery-card>
            </div>
            <div class="summery-item left five">
                <summery-card :summeryObj="totalPeoplePrice"></summery-card>
            </div>
        </div>
        <!-- 汇总 end -->
        
        <!-- table start  -->
        <div class="table-content" >
            <el-table :data="list" stripe height="660" @sort-change="sort">
                <el-table-column min-width="80" align="center" label="订单编号" sortable="true" prop="order_id">
                    <template slot-scope="scope">
                        <span>{{ scope.row.order_id}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="创建时间" sortable="true" prop="order_time">
                    <template slot-scope="scope">
                        <span>{{ scope.row.order_time}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="退款时间" sortable="true" prop="refunds_time">
                    <template slot-scope="scope">
                        <span>{{ scope.row.refunds_time}}</span>
                    </template>
                </el-table-column>                               
                <el-table-column min-width="80" align="center" label="餐桌名称/餐牌号">
                    <template slot-scope="scope">
                        <span>{{ (scope.row.seat || {}).seat_name }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="销售方式">
                    <template slot-scope="scope">
                        <span>{{ foodSaleWay.toString(scope.row.dine_way) }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="订单金额" sortable="true" prop="order_fee">
                    <template slot-scope="scope">
                        <span>{{ scope.row.order_fee }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="应收金额" sortable="true" prop="order_payable">
                    <template slot-scope="scope">
                        <span>{{ scope.row.order_payable}}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="实收金额" sortable="true" prop="paid_price">
                    <template slot-scope="scope">
                        <span>{{ scope.row.paid_price }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="支付方式">
                    <template slot-scope="scope">
                        <span>{{ formPayWay.toString(scope.row.pay_way) }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="是否开票">
                    <template slot-scope="scope">
                        <span>{{ OrderListStatue.toString(scope.row.order_status) }}</span>
                    </template>
                </el-table-column>
                <el-table-column min-width="80" align="center" label="操作">
                    <template slot-scope="scope">
                        <span class="blue-text">
                            <router-link :to="{path:'/orderform/orderdetail',query: { orderid:scope.row.order_id}}">
                                查看
                            </router-link>
                        </span>
                    </template>
                </el-table-column>                                                                                                                                
            </el-table>

            <empty-table v-if="isListEmpty"></empty-table>

            <!-- 分页 starat -->
            <div class="pagination-container">
                <el-pagination 
                @size-change="handleSizeChange" 
                @current-change="handleCurrentChange" 
                :current-page.sync="listQuery.page" 
                :page-sizes="[10,20,40]" 
                :page-size="listQuery.limit" 
                layout="sizes, jumper, prev, pager, next" 
                :total="total">
                </el-pagination>
            </div>
            <!-- 分页 end -->

        </div>
        <!-- table end  -->
    </div>
</template>

<script>
import moment from "moment";
import SummeryCard from "../components/summeryCard";
import SaleWay from "../components/saleWay";
import PayWay from "../components/payWay";
import SInvoice from "../components/invoice";
import EmptyTable from "@/components/emptytable/main"; //空列表

import { ls } from "@/config/pageStore";
import { orderList } from "../api";
import {
  foodSaleWay,
  formPayWay,
  IsInvoice,
  OrderListStatue
} from "@/config/cfg";

export default {
  components: {
    SummeryCard,
    SaleWay, //就餐方式
    PayWay, //支付方式
    SInvoice, //是否开票
    EmptyTable
  },
  data() {
    return {
      totalNum: {
        summeryName: "合计单数",
        summeryTotal: 0,
        summeryImg: require("../images/jishikaitai.png")
      },
      totalPrice: {
        summeryName: "订单金额",
        summeryTotal: 0,
        summeryImg: require("../images/mifan.png")
      },
      totalRealPrice: {
        summeryName: "退款金额",
        summeryTotal: 0,
        summeryImg: require("../images/yudingbiaofang.png")
      },
      totalAveragePrice: {
        summeryName: "订单均价",
        summeryTotal: 0,
        summeryImg: require("../images/dingdan.png")
      },
      totalPeoplePrice: {
        summeryName: "客单价",
        summeryTotal: 0,
        summeryImg: require("../images/xiaoshoue.png")
      },
      list: null, //表格数据

      //分页
      total: null, //分页总条数
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      //搜索
      orderBeginTime: "",
      orderEndTime: "",
      beginTime: "",
      endTime: "",
      orderId: "",
      dineWay: 0,
      payWay: 0,
      menuCardOrder: "",
      isInvoicing: 0,

      foodSaleWay: foodSaleWay,
      formPayWay: formPayWay,
      IsInvoice: IsInvoice,
      OrderListStatue: OrderListStatue,

      //排序
      isSort: false,
      sortLabel: "",
      sortOrder: 1,

      isListEmpty: true
    };
  },
  created() {
    this.listQuery.limit = ls.getItem("SLSTATISTICALLFORMSIZE") || 10;
    this.getList();
  },
  methods: {
    //拉取订单列表
    getList() {
      let data = {
        get_order_list: 1,
        order_status: OrderListStatue.REFUNDSUCCESS,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page
      };

      //创建时间开始
      if (this.orderBeginTime !== "") {
        data["order_begin_time"] = parseInt(
          moment(this.orderBeginTime).format("X")
        );
      }

      //创建时间结束
      if (this.orderEndTime !== "") {
        data["order_end_time"] = parseInt(
          moment(this.orderEndTime).format("X")
        );
      }

      //支付时间开始
      if (this.beginTime !== "") {
        data["begin_time"] = parseInt(moment(this.beginTime).format("X"));
      }

      //支付时间结束
      if (this.endTime !== "") {
        data["end_time"] = parseInt(moment(this.endTime).format("X"));
      }

      //餐桌号
      if (this.menuCardOrder !== "") {
        data["seat_name"] = this.menuCardOrder;
      }

      //订单标号
      if (this.orderId !== "") {
        data["order_id"] = this.orderId;
      }

      if (this.dineWay !== 0) {
        data["dine_way"] = this.dineWay; //用餐方式
      }
      if (this.payWay !== 0) {
        data["pay_way"] = this.payWay; //支付方式
      }
      if (this.isInvoicing !== 0) {
        data["is_invoicing"] = this.isInvoicing; //是否开票
      }

      //是否有排序
      if (this.isSort) {
        data["sort_name"] = this.sortLabel;
        data["desc"] = this.sortOrder;
      }

      orderList(data, this.getListHandler);
    },
    getListHandler(resp) {
      if (resp.ret === 0) {
        this.list = resp.data.order_list || [];

        if (this.list.length !== 0) {
          this.isListEmpty = false;
        } else {
          this.isListEmpty = true;
        }

        this.list = this.list.map(item => {
          if (item.order_time) {
            item["order_time"] = moment(item.order_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          if (item.refunds_time) {
            item["refunds_time"] = moment(item.refunds_time * 1000).format(
              "YYYY-MM-DD HH:mm:ss"
            );
          }
          return item;
        });

        this.total = resp.data.total;
        this.totalNum.summeryTotal = resp.data.total;
        this.totalPrice.summeryTotal = resp.data.all_order_fee + "元";
        this.totalRealPrice.summeryTotal = resp.data.get_real_order + "元";
        this.totalAveragePrice.summeryTotal =
          resp.data.order_average_price + "元";
        this.totalPeoplePrice.summeryTotal = resp.data.order_people_price + "元";
      } else {
        console.warn("获取列表错误！");
      }
    },
    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("SLSTATISTICALLFORMSIZE", val, 30);
      this.getList();
    },
    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },
    getData(name, value) {
      this[name] = value;
    },
    //排序
    sort(item) {
      this.sortLabel = item.prop;

      if (item.order === "descending") {
        this.isSort = true;
        this.sortOrder = -1;
      } else if (item.order === "ascending") {
        this.isSort = true;
        this.sortOrder = 1;
      } else {
        this.isSort = false;
      }

      this.getList();
    },

    checkData(startTime, endTime) {
      if (startTime === "" || endTime === "") {
        return true;
      }

      let start = parseInt(moment(startTime).format("X"));
      let end = parseInt(moment(endTime).format("X"));

      if (start > end) {
        this.$notify({
          title: "警告",
          message: "开始时间不能大于结束时间！",
          type: "warning"
        });
        return false;
      } else {
        return true;
      }
    },
    ordertimeStartChange() {
      let results = this.checkData(this.orderBeginTime, this.orderEndTime);
      if (!results) {
        this.orderBeginTime = "";
      }
    },
    ordertimeEndChange() {
      let results = this.checkData(this.orderBeginTime, this.orderEndTime);
      if (!results) {
        this.orderEndTime = "";
      }
    },
    beginTimeChange() {
      let results = this.checkData(this.beginTime, this.endTime);
      if (!results) {
        this.beginTime = "";
      }
    },
    endTimeChange() {
      let results = this.checkData(this.beginTime, this.endTime);
      if (!results) {
        this.endTime = "";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.search-content {
  @include fc(14px, #333);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
  height: 120px;
  padding: 20px 14px;
  margin-bottom: 10px;
  .search-top {
    margin-bottom: 10px;
  }
}
.search-top,
.search-bottom {
  min-width: 1650px;
  //   width: 310px;
  .search-item {
    margin-left: 80px;
    .search-label {
      height: 34px;
      line-height: 34px;
      margin-right: 14px;
      margin-left: 14px;
    }
    .search-detail.time {
      margin-left: 14px;
    }
  }
  .search-item:first-child {
    margin-left: 0px;
    width: 565px;
    .search-label {
      margin-left: 0;
    }
  }
  .search-item.seat {
    width: 312px;
  }
}

@media (max-width: 1900px) {
  .search-top,
  .search-bottom {
    min-width: 1450px;
    .search-item {
      margin-left: 10px;
    }
  }
}

.summery-content {
  height: 120px;
  margin-bottom: 10px;

  .summery-item {
    @include wh(20%, 100%);
    padding-right: 14px;
  }
  .summery-item.five {
    padding-right: 0;
  }
}
.table-content {
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
  padding: 14px;
  position: relative;
}

.pagination-container {
  text-align: center;
  padding: 50px 14px 6px;
}
.blue-text {
  @include fc(14px, #4877e7);
  cursor: pointer;

  a {
    @include fc(14px, #4877e7);
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#refundsuccess-orders {
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .search-text {
    .el-input {
      font-size: 14px;
      width: inherit;

      input {
        @include wh(180px, 34px);
        border-radius: 0;
      }
    }
  }

  .el-button {
    @include wh(100px, 34px);
    border: 1px solid #4877e7;
    color: #4877e7;
    border-radius: 0;
    font-size: 14px;
  }

  .el-table td,
  .el-table th {
    height: 60px;
  }

  .el-table .cell,
  .el-table th > div {
    padding-left: 5px;
    padding-right: 5px;
  }
  .el-date-editor.el-input {
    width: 240px;

    input {
      border-radius: 0;
      height: 34px;
    }
  }
}
</style>



