<template>
    <!-- 是否开票 下拉选择 component -->
  <div class="invoice-component">
    <el-select v-model="value" placeholder="请选择" @change="selectChange">
        <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
        </el-option>
    </el-select>
  </div>
</template>

<script>
import { IsInvoice } from "@/config/cfg";

export default {
  data() {
    return {
      options: [
        {
          value: 0,
          label: "全部"
        },
        {
          value: IsInvoice.YES,
          label: IsInvoice.toString(IsInvoice.YES)
        },
        {
          value: IsInvoice.NO,
          label: IsInvoice.toString(IsInvoice.NO)
        },
      ],
      value: ""
    };
  },
  methods: {
    selectChange() {
      this.$emit("invoice-change", this.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.invoice-component {
  display: inline-block;
}
</style>
<style lang="scss">
.invoice-component {
  .el-select {
    width: 180px;
    height: 34px;

    .el-input {
      font-size: 14px;

      input {
        border-radius: 0;
        height: 34px;
      }
    }
  }
}
</style>




