<template>
    <div id="order-form">
        <!-- 报表 menu start  :class="{'active':index === menuSelect}"-->
        <el-tabs v-model="activeName">
            <el-tab-pane label="全部" name="allform">
                <all-form v-if="isShowall"></all-form>
            </el-tab-pane>
            <el-tab-pane label="未支付" name="notpayform">
                <no-pay v-if="isShownopay"></no-pay>
            </el-tab-pane>
            <el-tab-pane label="已支付" name="payform">
                <pay-form v-if="isShowpay"></pay-form>
            </el-tab-pane>
            <el-tab-pane label="挂账" name="iouform">
                <iou-form v-if="isShowiou"></iou-form>
            </el-tab-pane>
            <el-tab-pane label="已反结" name="backform">
                <back-form v-if="isShowback"></back-form>
            </el-tab-pane>
            <el-tab-pane label="退款成功" name="refundfailform">
                <refund-fail v-if="isShowsuccess"></refund-fail>
            </el-tab-pane>
            <el-tab-pane label="退款失败" name="refundSuccessform">
                <refund-success v-if="isShowfail"></refund-success>
            </el-tab-pane>
            <el-tab-pane label="已关闭" name="closeform">
                <close-form v-if="isShowclose"></close-form>
            </el-tab-pane>
        </el-tabs>
        <!-- 报表 menu end -->
    </div>
</template>

<script>
import AllForm from "./allform";
import NoPay from "./notpayform";
import PayForm from "./payform";
import IouForm from "./iouform";
import BackForm from "./backform";
import RefundFail from "./refundfailform";
import RefundSuccess from "./refundsuccessform";
import CloseForm from "./closeform";

export default {
  components: {
    AllForm,
    NoPay,
    PayForm,
    IouForm,
    BackForm,
    RefundFail,
    RefundSuccess,
    CloseForm
  },
  data() {
    return {
      formType: [
        {
          name: "全部",
          path: "allform"
        },
        {
          name: "未支付",
          path: "notpayform"
        },
        {
          name: "已支付",
          path: "payform"
        },
        {
          name: "挂账",
          path: "iouform"
        },
        {
          name: "已反结",
          path: "backform"
        },
        {
          name: "退款成功",
          path: "refundsuccessform"
        },
        {
          name: "退款失败",
          path: "refundfailform"
        },
        {
          name: "已关闭",
          path: "closeform"
        }
      ],
      menuSelect: 0,

      activeName: "allform",

      isShowall: true,
      isShownopay: false,
      isShowpay: false,
      isShowiou: false,
      isShowback: false,
      isShowsuccess: false,
      isShowfail: false,
      isShowclose: false
    };
  },
  watch: {
    activeName(val) {
      switch (val) {
        case "allform":
          this.isShowall = true;
          break;
        case "notpayform":
          this.isShownopay = true;
          break;
        case "payform":
          this.isShowpay = true;
          break;
        case "iouform":
          this.isShowiou = true;
          break;
        case "backform":
          this.isShowback = true;
          break;
        case "refundfailform":
          this.isShowsuccess = true;
          break;
        case "refundSuccessform":
          this.isShowfail = true;
          break;
        case "closeform":
          this.isShowclose = true;
          break;
      }
    }
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#order-form {
  .el-tabs__header {
    @include fc(14px, #333);
    background-color: #fff;
    box-shadow: 0 2px 4px 0 #becaeb;
  }
  .el-tabs__item {
    height: 60px;
    line-height: 60px;
    @include fc(14px, #333);
    padding: 0 40px;
  }
  .el-tabs__active-bar {
    height: 4px;
    background-color: #4877e7;
  }
}
</style>


