<template>
    <!-- 就餐方式 下拉选择 component -->
  <div class="saleway-component">
    <el-select v-model="value" placeholder="请选择" @change="selectChange">
        <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
        </el-option>
    </el-select>
  </div>
</template>

<script>
import { foodSaleWay } from "@/config/cfg";

export default {
  data() {
    return {
      options: [
        {
          value: 0,
          label: "全部"
        },
        {
          value: foodSaleWay.PRESHOP,
          label: foodSaleWay.toString(foodSaleWay.PRESHOP)
        },
        {
          value: foodSaleWay.PACKSELF,
          label: foodSaleWay.toString(foodSaleWay.PACKSELF)
        },
        {
          value: foodSaleWay.TAKEOUT,
          label: foodSaleWay.toString(foodSaleWay.TAKEOUT)
        },
        {
          value: foodSaleWay.PACK,
          label: foodSaleWay.toString(foodSaleWay.PACK)
        }
      ],
      value: ""
    };
  },
  methods: {
    selectChange(){
        this.$emit('dineway-change', this.value);
    }
  }
};
</script>

<style lang="scss" scoped>
.saleway-component {
  display: inline-block;
}
</style>
<style lang="scss">
.saleway-component {
  .el-select {
    width: 180px;
    height: 34px;

    .el-input {
      font-size: 14px;

      input {
        border-radius: 0;
        height: 34px;
      }
    }
  }
}
</style>




